import './style.css';
import { Timeline } from './src/timeline.js';
import { csv, autoType, select } from "d3";

// document.querySelector('#app').innerHTML = `
//   <h1>Hello Vite!</h1>
//   <a href="https://vitejs.dev/guide/features.html" target="_blank">Documentation</a>
// `

// scroll offset in % from top; 0.5 means 50%
let offset = 0.5;
try {
  const searchParams = new URLSearchParams(location.search);
  offset = Number(searchParams.get("offset")) || 0.5;
} catch (e) {}

Promise.all([
  csv("./data/hospAdData.csv", autoType),
  csv("./data/infectionsurvey.csv", autoType),
  csv("./data/nationdeaths.csv", autoType),
  csv("./data/linkedEvents.csv", autoType),
]).then(function (aData) {

  const oTimeline = new Timeline(
    document.querySelector('#app'),
    {
      scrollOffset: offset,
      metric: "hospAdData",
      metricData: {
        hospAdData: aData[0],
        infectionsurvey: aData[1],
        nationdeaths: aData[2],
      },
      programmes: [
        {
          name: "Clap for carers",
          startDate: "2020-03-26",
          endDate: "2020-05-28",
          colour: "rgb(246,210,120)",
        },
        {
          name: "Eat out to help out",
          startDate: "2020-08-01",
          endDate: "2020-08-31",
          colour: "rgb(246,210,120)",
        },
        {
          name: "Euro 2020 football championship",
          startDate: "2021-06-11",
          endDate: "2021-07-11",
          colour: "rgb(246,210,120)",
        }],
      variants: [
        {
          name: "Native Type",
          Variant: "native",
          startDate: "2020-02-29",
        },
        {
          name: "Alpha",
          Variant: "alpha",
          startDate: "2020-11-30",
        },
        {
          name: "Delta",
          Variant: "delta",
          startDate: "2021-05-31",
        },
        {
          name: "Omicron BA.1",
          Variant: "Omicron BA.1",
          startDate: "2021-11-30",
        },
        {
          name: "BA.2",
          Variant: "Omicron BA.2",
          startDate: "2022-01-31",
        }],
      linkedEvents: aData[3],
    }
  );

  // Listen to metric change
  select("#select-metric").on("change", function (ev, d) {
    const selectedMetric = this.value;
    // update timeline data
    oTimeline.updateMetric(selectedMetric);
  })

});