import { timeFormat, timeYear } from "d3";
import { bmjColours } from "./constants";

export function wrapTextByWidth(zeText, width, fontSize, fontFace) {

    // fontSize doesn't need a unit - getTextWidth adds "px" automatically

    var outputArray = [];

    var words = zeText.split(/\s+/).reverse();
    var word;
    var line = [];
    var lastline;
    var linewords;
    var lineNumber = 1;
    var linewords2;

    while (word = words.pop()) {
        line.push(word);
        linewords = line.join(" ");

        var textWidth = getTextWidth(linewords, fontSize, fontFace);

        if (textWidth > width) {

            if (lastline.length == 1) {
                outputArray.push(lastline[0]);
            } else {
                outputArray.push(lastline);
            }

            line = [word];
            linewords = [word];
            lineNumber++;

        }

        lastline = linewords;

    }


    if (linewords.length == 1) {
        outputArray.push(linewords[0]);
    } else {
        outputArray.push(linewords);
    }

    return outputArray;


};

export function getTextWidth(text, fontSize, fontFace) {
    var canvas = document.createElement('canvas');
    var context = canvas.getContext('2d');
    context.font = fontSize + 'px ' + fontFace;
    return context.measureText(text).width;
}

export function bubbleList(zeID, inputText, maxWidth, maxHeight) {

    // console.log(inputText);

    var items = inputText.split(/[,]+/);
    // console.log(items);

    d3.select(zeID)
        .selectAll("g")
        .remove()

    if (inputText != "") {

        var xOffset = -10;
        var yOffset = -5;

        for (i = 0; i < items.length; i++) {

            var newItemWidth = (getTextWidth(items[i], 15, 'InterFace-Regular'));

            if (xOffset + newItemWidth > maxWidth) {
                xOffset = -10;
                yOffset += 30;
            }

            var newListItem = d3.select(zeID)
                .append("g");

            newListItem.append("circle")
                .attr("fill", "#A3AAB5")
                .attr("cx", 12 + xOffset)
                .attr("cy", yOffset)
                .attr("r", 12);

            newListItem.append("circle")
                .attr("fill", "#A3AAB5")
                .attr("cx", 12 + newItemWidth + xOffset)
                .attr("cy", yOffset)
                .attr("r", 12);

            newListItem.append("rect")
                .attr("fill", "#A3AAB5")
                .attr("x", 12 + xOffset)
                .attr("y", yOffset - 12)
                .attr("width", newItemWidth)
                .attr("height", 24);

            newListItem.append("text")
                .attr("fill", "#FFFFFF")
                .attr("x", 9 + xOffset)
                .attr("y", 5 + yOffset)
                .attr("font-family", "'InterFace-Regular'")
                .attr("font-size", "15px")
                .text(items[i]);

            xOffset += newItemWidth + 30;

        }

    }


};

export function bmjNumberString(zeNumber) {

    var numberAsString = zeNumber.toString();

    var formattedNumber = "";

    var counter = 0;

    for (var i = numberAsString.length - 1; i >= 0; i--) {

        if (counter == 3 && numberAsString.length > 4) {
            formattedNumber = numberAsString[i] + "\u2009" + formattedNumber;
            counter = 1;
        } else {
            formattedNumber = numberAsString[i] + formattedNumber;
            counter++;
        }

    }

    return formattedNumber;

}

export function variantFill(d) {
    if (d["Variant"] == "native") {
        return bmjColours.blue;
    } else if (d["Variant"] == "alpha") {
        return bmjColours.pink;
    } else if (d["Variant"] == "delta") {
        return bmjColours.aqua;
    } else if (d["Variant"] == "Omicron BA.1") {
        return bmjColours.orange;
    } else if (d["Variant"] == "Omicron BA.2") {
        return bmjColours.yellow;
    }
}

const formatMonth = timeFormat("%B %y"),
    formatYear = timeFormat("%Y");

export function timeMultiFormat(date) {
  return (timeYear(date) < date ? formatMonth
      : formatYear)(date);
}